import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'
Vue.use(VueAxios, axios)

/* 参数说明
urlaxios:确定基础路径
    0 产品数据：https://api-hmugo-web.itheima.net/api/public/v1/,
    1 注册登录:http://www.kangliuyong.com:10002/
params:axios请求参数对象
keepData:储存返回数据
*/

export const reaxios = (urlaxios, params) => {
    // 基础路径
    var baseUrl = ''
    if (urlaxios == 0) {
        baseUrl = "https://api-hmugo-web.itheima.net/api/public/v1";
    } else {
        baseUrl = "http://www.kangliuyong.com:10002";
        params.data.appkey = "U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA="
    }

    // 请求方式为post
    if (params.method == 'post' || params.method == 'POST') {
        // 配置请求头
        axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=utf-8';
        // 请求前预处理
        let paramsString = '';
        for (let key in params.data) {
            paramsString += key + '=' + params.data[key] + '&'
        }
        params.data = paramsString.slice(0, -1);
        params.url = params.url + '?' + params.data
    }

    return axios({
        ...params,
        url: baseUrl + params.url,
    }).catch(err => {
        console.log(err);
    })
}